.post {
  max-width: 1200px;
  margin: 0 auto;
  list-style-type: none;

  &__item {
    background-color: cornsilk;
    padding: 20px;
    margin-bottom: 40px;
  }

  &__title {
    color: darkblue;
  }

  &__text {
    font-weight: 500;
    font-size: 17px;
    margin: 40px 0;
  }

  &__user {
    color: darkcyan;
  }

  &__email {
    margin-bottom: 30px;
    color: rgb(2, 177, 177);
  }
}
